.navbar-position {
  position: fixed;
  top: 0px;
  background-color: rgba(195, 24, 201, 0.5);
  width: 100%;
  height: 63px;
  z-index: 1000;
  margin-left: -4px;
}

.home {
  margin-left: 500px;
}

.titulo-nav {
  display: inline-flex;
  margin: 10px 10px 0px;
  padding: 10px;
  border-radius: 4px;
  text-decoration: none;
  color: #fff;
  text-transform: capitalize;
  font-weight: bold;
}

.titulo-nav:hover {
  background-color: rgba(255, 255, 255, 0.466);
  padding: 10px;
}

.estiloGenero {
  width: 45%;
  height: auto;
  margin: 80px auto;
  border-radius: 10px;
  padding: 20px;
  background-color: #fbfbfb;
}
/* 
.form-antes {
  display: flex;
  flex-direction: column;
  text-align: center;
} */

.estiloGenero h1,
h2 {
  color: #ab4493;
  text-align: center;
}

.estiloGenero hr {
  border-width: 2px;
  border-color: #ab4493;
}

.estiloGenero input {
  background-color: #fbfbfb;
  width: 260px;
  height: 40px;
  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
  border-color: #ab4493;
  margin-top: 10px;
  padding-left: 10px;
  margin-bottom: 20px;
}

.estiloGenero label {
  display: block;
  float: center;
}

.estiloGenero button {
  height: 70px;
  padding-left: 5px;
  padding-right: 5px;
  margin-bottom: 20px;
  margin-top: 10px;
  background-color: #ab4493;
  border-color: #ab4493;
  border-style: solid;
  border-radius: 10px;
  width: 80px;
  cursor: pointer;
  color: #ffff;
}

.estiloGenero span {
  color: #ab4493;
}

.aviso {
  font-size: 13px;
  color: #0e0e0e;
}

.estiloGenero p {
  font-size: 14px;
  color: #0e0e0e;
}

::-webkit-input-placeholder {
  color: #a8a8a8;
}

.estiloGenero input:focus {
  outline: 0;
  border: 1px solid #cc32eb;
}

.cardGenero {
  border-color: #ab4493;
  border-style: solid;
  border-radius: 10px;
  margin: 30px;
  height: 85px;
  width: 85%;
}

.cardGenero tr {
  margin-left: 50px;
  width: 100%;
}

.cardGenero td {
  width: 100px;
}

.cardGenero tr button {
  margin-left: 50px;
}

.cardGenero button {
  height: 50px;
  width: 60px;
  margin: 0px 20px 0px 0;
  border: none;
}

.cardGenero ion-icon {
  height: 25px;
  width: 25px;
}

.cardGenero button:hover {
  background-color: var(--lightpink);
}

.cardGenero tr {
  display: flex;
  justify-content: center;
  align-items: center;
}

.cardGenero tr td {
  margin: 15px;
}

.formGenero {
  display: flex;
  justify-content: center;
  border-bottom: pink solid 2px;
}

.formGenero form {
  display: flex;
  flex-direction: column;
  margin-bottom: 35px;
  width: 300px;
}

.formGenero tr {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  margin-bottom: -20px;
}

.formGenero p {
  margin-bottom: 0px;
  margin-left: 0;
}

.formGenero tr td {
  margin-left: 0px;
}

.colocar_nombre {
  margin-left: 20px;
}

.formGenero button {
  background-color: #fbfbfb;
  border: none;
  padding: 0;
}

.formGenero button ion-icon {
  background-color: #fbfbfb;
  color: var(--darkpink);
  cursor: pointer;
  width: 100%;
  height: 100%;
  transition: color 0.2s;
}

.formGenero button ion-icon:hover {
  color: var(--lightpink);
}

.boton-form {
  background-color: var(--darkpink);
  border-radius: 8px;
  padding: 10px;
  width: 100%;
  cursor: pointer;
  margin-top: 20px;
  margin-bottom: 20px;
  color: white;
  font-size: 15px;
  border: none;
  transition: background-color 0.2s;
}

.boton-form:hover {
  background-color: var(--lightpink);
}

.form {
  margin-top: 2px;
  display: flex;
  flex-direction: column;
  width: 350px;
}

.form p {
  font-size: 20px;
}

.form input {
  width: 95.25%;
  margin-bottom: 20px;
  border-radius: 5px;
  height: 3em;
  border-color: var(--lightpink);
  outline-color: var(--darkpink);
  padding-left: 10px;
}

.form input::-webkit-input-placeholder {
  color: grey;
  font-size: 1.2em;
  font-style: italic;
  opacity: 0.8;
}

.form input:-ms-input-placeholder {
  color: grey;
  font-size: 1.2em;
  font-style: italic;
  opacity: 0.8;
}

.form input::placeholder {
  color: grey;
  font-size: 1.2em;
  font-style: italic;
  opacity: 0.8;
}

.form h2 {
  font-size: 35px;
  color: var(--darkpink);
}

.form select {
  width: 100%;
  margin-bottom: 20px;
  height: 3em;
  border-color: var(--lightpink);
  border-radius: 5px;
  outline-color: var(--darkpink);
  color: var(--lightpink);
  font-size: 0.95em;
  font-style: italic;
  opacity: 0.8;
}

.form h2 {
  margin-top: 0px;
  margin-bottom: 2px;
}

.form-label {
  font-size: 13px;
  display: flex;
}

.form-label span {
  color: var(--darkpink);
  margin-right: 3px;
}

.form-label label {
  margin-right: 3px;
}

.modal-contenido {
  background-color: rgb(231, 240, 240);
  width: 400px;
  padding: 20px 20px;
  margin: 10% auto;
  position: relative;
  border-radius: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  z-index: 10;
}

.modal-close {
  justify-content: flex-start;
  width: 100%;
}

.modal-close button {
  font-size: 35px;
  position: absolute;
  top: 0px;
  left: 0px;
  color: rgb(143, 143, 143);
  background: none;
  border: none;
  cursor: pointer;
  margin-top: 2%;
  transition: color 0.2s;
}

.modal-close button:hover {
  color: var(--lightpink);
}

